// SVG
.svg__logo {
    overflow: visible !important;

	max-width: 150vw;
    position: absolute;
    top: -10%;
    left: 43%;
    transform: translate(-50%, -50%);

	@include media('<tablet'){
		top: 0;
	}
	@include media('<phone'){
		top: 5%;
	}
}

.header_logo{
	height: 10vh;
	left: 0.8vw;

	@include media('<phone'){
		height: 11vh;
		left: 0;
	}
}

.section_logo {
    height: 100vh;
    // display: flex;
    align-items: center;
    justify-content: center;  
}

.section_logo svg {
    width: 60%;

	@include media('<tablet'){
		width: 80%;
		top: 20vw;
	}

	@include media('<phone'){
		width: 80%;
		top: 50vw;
	}
}

.scroll-letter {
    fill: #fff;
}

.scroll-drone {
    fill: #fff;
	// stroke: #fff;
}

.line {
	fill: none;
