.grid-section {
    width: 100%;
    overflow: visible;
    z-index: 2;

	@include media('<phone'){
		height: 120vh;
	}
}

.grid-layout {
    display: flex;
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1rem;
    transform-origin: center top;

	@include media('<phone'){
		grid-template-columns: 1fr 1fr;
		transform-origin: center top;
	}
}

.column {
    height: 100%;
}

