}

.panel {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.column-content {
    display: grid;
    // grid-template-rows: 45vh 45vh 45vh;
	grid-template-rows: 45vh 45vh;
    grid-gap: 1rem;
    height: 100%;
}

.grid-image {
    width: 100%;  	
}

